@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import url('https://fonts.googleapis.com/css2?family=Swanky+and+Moo+Moo&display=swap');

* {
  margin: 0;
  padding: 0;
}

:root {
  --linkcolor: #3cdbc0;
  --textcolor: rgb(209, 206, 206);
  --raleway: 'Raleway', sans-serif;
  --swanky: 'Swanky and Moo Moo', cursive;
}

body {
  font-family: var(--raleway);
  background-color: #202020;
}

.sidebar-top {
  grid-area: sidebar-top;
}

.sidebar-mid {
  grid-area: sidebar-mid;
}

.sidebar-bot {
  grid-area: sidebar-bot;
}

.leftpanel {
  grid-area: leftpanel;
  color: var(--textcolor);
}

.rightpanel {
  grid-area: rightpanel;
}

@media (max-width: 991px) {
  .container {
    display: grid;
    grid-template-columns: 8fr 4fr;
    grid-template-rows: auto;
    grid-template-areas:
      "sidebar-mid sidebar-bot"
      "leftpanel leftpanel";
  }
  
  .instaimg, .rightpanel {
    display: none;
  }

  .leftpanel {
    border-top: 1px solid var(--textcolor);
    padding: 20px 30px;
  }

  .sidebar-mid {
    margin: left;
    padding: 6px 10px;
  }

  .sidebar-bot {
    margin: right;
    padding: 6px 10px;
    justify-self: right;
  }

  .sidebar-mid ul li, .sidebar-bot ul li {
    display: inline-block;
    margin-top: 4px;
  }

  .sidebar-mid ul li {
    padding: 0 10px;
  }

  .sidebar-bot ul li {
    padding: 0 5px;
  }

  .sidebar ul li .active {
    color: var(--linkcolor);
  }

  .material-icons {
    font-size: 30px;
  }

  a {
    color: var(--textcolor);
  }

  .githubicon {
    color: var(--textcolor);
    font-size: 30px;
  }

}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) {

  .container {
    display: grid;
    height: 100vh;
    grid-template-columns: 4% 56% 40%;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas:
      "sidebar-top leftpanel rightpanel"
      "sidebar-mid leftpanel rightpanel"
      "sidebar-bot leftpanel rightpanel";
  }
  
  .sidebar {
    margin: auto;
  }

  .sidebar ul {
    list-style-type: none;
    margin: auto;
  }

  .sidebar ul li {
    padding: 10px 0;
    margin: auto;
    border-bottom: 1px solid transparent;
  }

  .sidebar ul li span:hover {
    color: var(--linkcolor);
  }

  .sidebar ul li .active {
    border-bottom: 1px solid var(--linkcolor);
    color: var(--linkcolor);
  }

  .sidebar ul {
    margin: auto;
  }

  .sidebar-top {
    margin-top: 0;
  }

  .leftpanel, .rightpanel {
    background-color: #252525;
  }

  .leftpanel, .rightpanel {
    padding: 115px 75px;
  }

  .home h1 {
    font-size: 80px;
    font-weight: 400;
    font-family: var(--swanky);
    margin-top: -30px;
  }

  .home h2 {
    font-size: 45px;
    margin-top: 20px;
    font-weight: 300;
    font-family: var(--swanky);
  }

  .skills h2 {
    font-size: 30px;
    margin-top: 30px;
    font-weight: 300;
  }

  .skills h2:last-of-type {
    color: var(--linkcolor);
    margin-top: 40px;
  }

  .skills ul {
    font-size: 20px;
    margin: 15px 0 0 40px;
    font-weight: 300;
    list-style-type: none;
  }

  .skills i {
    color: var(--linkcolor);
  }

  .skills p {
    font-size: 20px;
    padding: 5px 0;    
  }

  .skills p:first-of-type {
    margin-top: 40px;
  }

  .skills p:last-of-type {
    margin-top: 40px;
  }

  .greenspan {
    color: var(--linkcolor);
    font-weight: 500;
  }

  .rightpanel i {
    color: var(--textcolor);
    padding: 10px;
    font-size: 5vw;
    margin-top: 15vh;

  }

  .rightpanel h1, 
  .rightpanel h2 {
    color: var(--textcolor);
    font-family: var(--swanky);
    margin-top: 15vh;
    font-size: 4vw;
    font-weight: 400;
  }

  .material-icons {
    font-size: 30px;
  }

  .sidebar-bot {
    margin-bottom: 10px;
  }

  .githubicon {
    color: var(--textcolor);
    font-size: 2.5vw;
    margin: auto;
  }

  .githubicon:hover {
    color:var(--linkcolor);
  }

  .instaimg {
    width: 100%;
  }

  a {
    color: var(--textcolor);
  }

  .aboutbtn {
    background-color: #38bba5;
    color: #252525;
    border: none;
    border-radius: 15px;
    font-family: var(--swanky);
    font-weight: 600;
    font-size: 36px;
    padding: 16px;
    margin-top: 60px;
    cursor: pointer;
  }

  .aboutbtn:hover {
    background-color: var(--linkcolor);
    font-weight: 600;
  }

  .fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    -webkit-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    }
    @-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
    }
    @keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
    } 
}

